import ItemObras from './ItemObras'
import { mapState } from 'vuex'
import axios from 'axios'
import { baseApiUrl } from '../../global'
export default {
  name: 'obrasPrivadas',
  components: { ItemObras },
  data() {
    return {
      obras: [],
      search: 'A',
      status: [],
    }
  },

  computed: {
    filteredItems() {
      const id = this.search

      const buscar = this.status.filter((e) => {
        return e.id === id
      })
      return buscar
    },
    filtershelves() {
      const a = this.obras.filter((e) => {
        return e.terminada === this.search
      })
      return a
    },
    ...mapState(['usuario']),
  },

  methods: {
    getObrasPrivadas() {
      const url = ` ${baseApiUrl}/mesa/${this.$store.state.usuario.id}/obrasprivadas`
      axios(url).then((res) => {
        this.obras = this.obras.concat(res.data)
      })
    },

    getstatus() {
      const url = ` ${baseApiUrl}/mesa/${this.$store.state.usuario.id}/obrasprivadas/status`
      axios(url).then((res) => {
        this.status = res.data
      })
    },
  },
  mounted() {
    this.getObrasPrivadas()
    this.getstatus()
  },
}
